.closePuppy {
    position: absolute;
    right: 32px;
    top: 62px;
    width: 32px;
    height: 32px;
  }
  .closePuppy:hover {
    opacity: 0.5;
    cursor: pointer;
  }
  .closePuppy:before, .closePuppy:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: black;
  }
  .closePuppy:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  .closePuppy:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }

  .slider {
    -webkit-animation: slideIn 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
            animation: slideIn 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  }
  
  @-webkit-keyframes slideIn {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-500rem, 0, 500rem);
              transform: translate3d(-500rem, 0, 500rem);
    }
    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
  }
  
  @keyframes slideIn {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-500rem, 0, 500rem);
              transform: translate3d(-500rem, 0, 500rem);
    }
    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
  }

